<template>
    <!-- <the-menu /> -->
    <div class="book-view">
        <div class="list">
            <div class="list-item" v-for="item in 20" :key="item">
                <p>{{ item }}</p>
            </div>
        </div>
    </div>
</template>

<script>
// import TheMenu from'../components/TheMenu.vue';
import {mapGetters} from 'vuex';
export default {
    data() {
        return {
           
        }
    },
    components: {
        // TheMenu
    },
    computed:{
        ...mapGetters([
            'getPosition'      
        ])
    },
    mounted() {
        let tmp = document.querySelectorAll(".list-item>p")
        this.getPosition.forEach(item => {
            tmp[item].style=`color:#000`;
        })
        for (let i = 0; i<9;i++)
        {
            tmp[i].innerText=`0${i+1}`;
        }
    }
}
</script>

<style lang="scss" scoped>
@import "../assets/style/variables";

.book-view {

    height: 100vh;

    .list {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-gap:20px;
        &-item
        {
            p
            {
                font-size: 200%;
                // font-weight: bold;
                font-family: 'Alfa Slab One', cursive;
                // color:#dbdada;;
                color:rgba(112, 112, 112, 0.3);
                cursor:pointer;
            }
        }
    }
}
</style>